'use client'

import { useState } from 'react'
import { 
  CheckCircleIcon, 
  XCircleIcon,
  CreditCardIcon,
  ShieldCheckIcon,
  ArrowPathIcon
} from '@heroicons/react/24/outline'

export default function Abonnement() {
  const [selectedPlan, setSelectedPlan] = useState('basic')

  const plans = [
    {
      id: 'basic',
      name: 'Grunnleggende',
      price: '499',
      features: [
        'Opptil 50 kunder',
        '10 inspeksjoner per måned',
        'Grunnleggende rapportering',
        'E-post støtte',
        '5 brukere'
      ]
    },
    {
      id: 'pro',
      name: 'Profesjonell',
      price: '999',
      features: [
        'Opptil 200 kunder',
        'Ubegrenset inspeksjoner',
        'Avansert rapportering',
        'Prioritert støtte',
        '15 brukere',
        'API-tilgang'
      ]
    },
    {
      id: 'enterprise',
      name: 'Enterprise',
      price: '1999',
      features: [
        'Ubegrenset kunder',
        'Ubegrenset inspeksjoner',
        'Tilpasset rapportering',
        '24/7 støtte',
        'Ubegrenset brukere',
        'API-tilgang',
        'Dedikert kundesuksess'
      ]
    }
  ]

  return (
    <div className="p-8">
      <div className="max-w-7xl mx-auto">
        <div className="text-center mb-12">
          <h1 className="text-4xl font-bold text-white mb-4">Velg din plan</h1>
          <p className="text-gray-400">Velg den planen som passer best for din virksomhet</p>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
          {plans.map((plan) => (
            <div
              key={plan.id}
              className={`rounded-xl border p-6 transition-all ${
                selectedPlan === plan.id
                  ? 'border-blue-500 bg-gray-800'
                  : 'border-gray-700 bg-gray-900 hover:border-gray-600'
              }`}
            >
              <div className="flex justify-between items-start mb-6">
                <div>
                  <h3 className="text-xl font-semibold text-white">{plan.name}</h3>
                  <p className="text-gray-400 mt-1">Per måned</p>
                </div>
                <div className="text-right">
                  <span className="text-3xl font-bold text-white">kr {plan.price}</span>
                  <p className="text-sm text-gray-400">/mnd</p>
                </div>
              </div>

              <ul className="space-y-4 mb-8">
                {plan.features.map((feature, index) => (
                  <li key={index} className="flex items-center text-gray-300">
                    <CheckCircleIcon className="w-5 h-5 text-green-500 mr-3" />
                    {feature}
                  </li>
                ))}
              </ul>

              <button
                onClick={() => setSelectedPlan(plan.id)}
                className={`w-full py-3 px-4 rounded-lg font-medium transition-colors ${
                  selectedPlan === plan.id
                    ? 'bg-blue-600 text-white hover:bg-blue-700'
                    : 'bg-gray-800 text-white hover:bg-gray-700 border border-gray-700'
                }`}
              >
                {selectedPlan === plan.id ? 'Valgt plan' : 'Velg plan'}
              </button>
            </div>
          ))}
        </div>

        <div className="mt-16 grid grid-cols-1 md:grid-cols-3 gap-8">
          <div className="bg-gray-800 rounded-xl p-6 border border-gray-700">
            <CreditCardIcon className="w-8 h-8 text-blue-500 mb-4" />
            <h3 className="text-lg font-semibold text-white mb-2">Sikker betaling</h3>
            <p className="text-gray-400">Alle betalinger er krypterte og sikre</p>
          </div>
          <div className="bg-gray-800 rounded-xl p-6 border border-gray-700">
            <ShieldCheckIcon className="w-8 h-8 text-green-500 mb-4" />
            <h3 className="text-lg font-semibold text-white mb-2">Garantert tilfredshet</h3>
            <p className="text-gray-400">30 dagers pengene tilbake-garanti</p>
          </div>
          <div className="bg-gray-800 rounded-xl p-6 border border-gray-700">
            <ArrowPathIcon className="w-8 h-8 text-purple-500 mb-4" />
            <h3 className="text-lg font-semibold text-white mb-2">Enkel oppgradering</h3>
            <p className="text-gray-400">Oppgrader eller nedgrader når som helst</p>
          </div>
        </div>
      </div>
    </div>
  )
} 